					that.items.push(that.items[0]);
					that.items.shift();
					con1.style.marginTop = '0px';
					that.animate = !that.animate; // 这个地方如果不把animate 取反会出现消息回滚的现象，此时把ul 元素的过渡属性取消掉就可以完美实现无缝滚动的效果了
