			},
			pchd(index) {
				this.start = index;
				if(index == 2) {
					this.top = -140;
				} else if(index == 1) {
					this.start = 5;
				} else {
					this.top = 0
				}
			},
			pchc(event) {
				event.cancelBubble = true;
				this.start = 5;
			},
			lootery_tab_head_hg(index) {
				this.title_start = index;
			},
			sjxz(star) {

				let timer = 0;
				let timers = 0;
				let that = this;

				//定义生成最小到最大值的随机函数
				function rand(min, max) {
					return parseInt(Math.random() * (max - min + 1) + min);
				}

				function start(min, max, length) {
					//定义空数组
					var arr = [];

					while(arr.length < length) {
						//生成一个随机数prem
						var prem = rand(min, max);
						//判断生成的随机数prem是否在数组arr里,果然不在，就将这个随机数插入到数组里，如果在，执行下一次循环
						if(arr.indexOf(prem) == -1) {

							arr.push(prem);
						}
					}
					//返回数组arr
					return arr;
				}
				
				
				function getRandomArrayElements(arr, count) {
				    var shuffled = arr.slice(0)
				    var i = arr.length 
				    var min = i - count
				    var temp; 
				    var index;
				    while (i-- > min) {
				        index = Math.floor((i + 1) * Math.random());
				        temp = shuffled[index];
				        shuffled[index] = shuffled[i];
				        shuffled[i] = temp;
				    }
				    return shuffled.slice(min);
				}

				clearInterval(timer);
				timer = setInterval(function() {
					if(star == 0){
						that.zjjg = [];
						that.zjjg = start(1, 9, 5)	
					}else if(star == 1){
						that.zjjger = [];
						that.zjjger = start(1, 9, 7)	
						that.shenxx =  getRandomArrayElements(that.shenx, 7)
					}else if(star == 2){
						that.zjjgsa = [];
						that.zjjgsa = start(1, 9, 5)							
					}else if(star == 3){
						that.zjjgsi = [];
						that.zjjgsi = start(1, 9, 5)	
					}else if(star == 4){
						that.zjjgwu = [];
						that.zjjgwu = start(1, 9, 5)	
					}
				}, 50)

				setTimeout(function() {
					clearInterval(timer);
				}, 2000)

			},
			startChange: function() {
				var _this = this;
				setInterval(function() {
					if(_this.index < _this.imgs.length - 1) {
						_this.index++
					} else {
						_this.index = 0
					}
					_this.currImgs.splice(0, 1, _this.imgs[_this.index]);
				}, 2000);
			},
			news_huad(index) {
				this.newsd = index;
				console.log(this.index)
			}
		},
		mounted: function() {
			let that = this;
			this.currImgs = [this.imgs[0]];
			this.startChange();

			setInterval(this.scroll, 1000)
		
			console.log(1)
		},
		components: {
			cheader,
			footers
		}
	}
</script>

